body {
  font-family: IRANSans,FontAwesome sans-serif !important;
  background-color: #fafafa;
}

a {
  background-color: transparent;
  color: #d65611;
  text-decoration: none;
  cursor: pointer;
}

a:hover {
  text-decoration: none;
  color: #d68c06;
}

.text-orange {
  color: #d65611 !important;
}

.swiper-pagination-bullet {
  width: 16px;
  height: 16px;
  display: inline-block;
  border-radius: 100%;
  background: #7e7e7e;
  opacity: .5;
}

.swiper-pagination-bullet-active {
  opacity: 1;
  background: #d65611;
  width: 16px;
  height: 16px;
}

.con-sidebar .vs-sidebar {
  width: calc(100% - 20px);
  max-width: 270px;
  height: calc(100% - 50px) !important;
  margin-top: 50px;
  position: absolute;
  right: 0;
  top: 0;
  -webkit-box-shadow: 5px 0 20px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 5px 0 20px 0 rgba(0, 0, 0, 0.1);
  background: #fff;
  display: block;
  -webkit-transform: translate(0);
  transform: translate(0);
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}

div.sidebarx-enter div.vs-sidebar, div.sidebarx-leave-to div.vs-sidebar {
  -webkit-transform: translate(100%) !important;
  transform: translate(100%) !important;
}

.link-orange-active {
  color: #9c3f11;
  font-weight: bold;
}

.container-fluid {
  padding-left: 3rem !important;
  padding-right: 3rem !important;
  max-width: 1440px;
  margin: 0 auto;
}

.border-orange:focus {
  background-color: rgba(214, 86, 17, 0.5);
}

.form-control:focus {
  border-color: rgba(214, 86, 17, 0.8);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 8px rgba(214, 86, 17, 0.6);
  outline: 0 none;
}

@media (max-width: 599px) {
  .container-fluid {
    padding-left: 0rem !important;
    padding-right: 0rem !important;
  }
}



